import { BlogPost } from './../blog-post';
import { Component, OnInit, Input } from '@angular/core';
import { TruncatePipe } from '../truncate.pipe';

@Component({
  selector: 'app-blog-post-title',
  templateUrl: './blog-post-title.component.html',
  styleUrls: ['./blog-post-title.component.scss']
})
export class BlogPostTitleComponent implements OnInit {

  @Input() post: BlogPost;
  fullSummary:string;

  constructor(private truncate :TruncatePipe) {
    
  }

  ngOnInit() {
    this.fullSummary=this.post.summary;
    this.post.summary=this.truncate.transform(this.post.summary,20);

  }

  showFullSummary(){
    this.post.summary=this.fullSummary;
  }

  markFav(){
    this.post.isFav=!this.post.isFav;
    if(this.post.isFav){
      this.post.favoritText="unfavorite"
    }else{
      this.post.favoritText="favorite"
    }
  }

}
