import { BlogPostTitleComponent } from './../blog-post-title/blog-post-title.component';
import { BlogPost } from './../blog-post';
import { Component, OnInit, ViewChildren, QueryList } from '@angular/core';
import { BlogDataService } from '../blog-data.service';

@Component({
  selector: 'app-blog-list',
  templateUrl: './blog-list.component.html',
  styleUrls: ['./blog-list.component.scss']
})
export class BlogListComponent implements OnInit {

  blogPosts: BlogPost[][] = [[], [], [], []];
  currentPage: number;
  @ViewChildren('tile') blogPostTileComponents: QueryList<BlogPostTitleComponent>;

  constructor(private blogDataServise: BlogDataService) {

  }

  ngOnInit() {

    this.currentPage = 0;
    this.blogPosts = this.blogDataServise.getData();


  }

  updatePage(newPage) {
    this.currentPage = newPage;
  }

  expandAll() {
    this.blogPostTileComponents.forEach(e => e.showFullSummary());
  }
  favAll(){
    this.blogPosts[this.currentPage]
    .forEach((p)=>{
      p.isFav=true;
      if(p.isFav){
        p.favoritText="unfavorite"
      }else{
        p.favoritText="favorite"
      }
    });
  }

}
